// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-aria-modal, %eureka-aria-modal {
    $this: '.eureka-aria-modal';

    &__overlay {
        @include eureka-scroll-y;
        width: 100%;
        height: 100%;
        background-color: var(--eureka-modal-overlay-color-bg, eureka-light-theme('modal-overlay-color-bg'));
        box-sizing: border-box;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1050;
    }

    &__dialog {
        max-width: 100%;
        background-color: var(--eureka-core-color-canvas, eureka-light-theme('core-color-canvas'));
        box-sizing: border-box;
        cursor: default;
        display: block;

        &:focus {
            outline: none;
        }
    }

    &--overlay-clickable {
        cursor: pointer;
    }
}
