// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/effects.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/vars/breakpoints.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-modal, %eureka-modal {
    @include eureka-flex-column;
    @include eureka-box-shadow-elevated(eureka-light-theme('core-color-black'));
    border-radius: var(--eureka-modal-dialog-border-radius, eureka-light-theme('modal-dialog-border-radius'));
    max-width: 90%;
    max-height: 90%;
    box-sizing: border-box;
    margin: auto;
    position: relative;
    $this: '.eureka-modal';

    &__overlay {
        @include eureka-flex-column;
        align-items: center;
    }

    &__content-container {
        @include eureka-flex-column;
    }

    &__main-container {
        > :first-child {
            margin-top: 0;
        }

        > :last-child {
            margin-bottom: 0;
        }
    }

    &__content-container,
    &__main-container {
        min-height: 1px;
        flex: 1 1 auto;
    }

    &__close-button {
        position: absolute;
        top: 8px;
        right: 8px;

        .eureka-button__content-container {
            padding: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
        }
    }

    &__actions-container {
        @include eureka-flex-row;
        justify-content: space-around;
    }

    &__actions-container-inner {
        @include eureka-flex-row;
        max-width: 876px;
        justify-content: space-around;
        flex-wrap: wrap-reverse;
        flex: 1 1 auto;
        gap: #{eureka-to-px(eureka-light-theme('core-system-scale') * 3)};

        .eureka-button {
            max-width: 432px;
            min-width: calc(50% - #{eureka-to-px(eureka-light-theme('core-system-scale') * 1.5)});
            font-size: inherit;
            flex: 1 1 auto;
        }
    }

    &__heading-container,
    &__actions-container {
        flex: 0 0 auto;
    }

    &__heading-container,
    &__main-container,
    &__actions-container {
        box-sizing: border-box;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 4)};
    }

    * + &__main-container {
        padding-top: 0;
    }

    * + &__actions-container {
        padding-top: 0;
    }

    &__close-button + * {
        margin-top: 40px;
    }

    &__heading-container + &__close-button + * {
        margin-top: 0;
    }

    &__title {
        @include eureka-h5;
    }

    &__sub-title {
        @include eureka-max-line-width;
        @include eureka-sub-text;
        color: var(--eureka-core-color-text-alt, eureka-light-theme('core-color-text-alt'));
        margin: 0;
    }

    &--has-close-btn {
        #{$this}__heading-container {
            padding-right: 3.3333em;
        }
    }

    &__overlay.eureka-aria-modal__overlay {
        opacity: 0;
        transition: opacity 150ms;
    }

    &__overlay.eureka-aria-modal__overlay--after-open {
        opacity: 1;
    }

    &__overlay.eureka-aria-modal__overlay--before-close {
        opacity: 0;
    }

    @include eureka-breakpoint-max-only($eureka-xs-max) {
        &--is-bottom-sheet {
            display: block;

            .eureka-modal {
                width: 100%;
                max-width: initial;
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
                transition: transform 0.25s linear;
                transform: translate3d(0, 100%, 0);
                position: absolute;
                top: 100%;
            }

            &.eureka-aria-modal__overlay--after-open .eureka-modal {
                transform: translate3d(0, 0, 0);
                top: auto;
                bottom: 0;
            }
        }
    }
}
